.barra {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    @include tablet {
        flex-direction: row-reverse; 
        justify-content: space-between;
    }
}

.navegacion {
    @include tablet {
        display: flex;
        gap: 2rem;
        align-items: center;
    }

    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        font-size: 1.8rem;
    }
}
